.stocks {
  &__inner {
    margin-bottom: 100px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 24px;

    @media(max-width: 1330px) {
      grid-template-columns: repeat(3, 1fr);
      margin-bottom: 80px;
    }
    @media(max-width: 1024px) {
      grid-gap: 18px
    }
    @media(max-width: 767px) {
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 16px;
      margin-bottom: 60px;
    }
  }

  &__item {
    border: 1px solid #E8E8E8;
    border-radius: 6px;
    margin-bottom: 6px;

    @media(max-width: 767px) {
      margin-bottom: 9px;
    }
  }

  &__image {
    max-width: 100%;

    img,
    source {
      object-fit: cover;
      border-radius: 6px 6px 0px 0px;
      width: 100%;
      height: 221px;

      @media(max-width: 1024px) {
        height: 161px;
      }
      @media(max-width: 767px) {
        height: 180px;
      }
      @media(max-width: 550px) {
        height: 140px;
      }
      @media(max-width: 424px) {
        height: 96px;
      }
    }
  }

  &__info {
    padding: 20px 16px;

    @media(max-width: 1024px) {
      padding: 16px 12px;
    }
    @media(max-width: 767px) {
      padding: 12px 6px;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 18px;
    line-height: 130%;
    color: #292929;
    margin-bottom: 35px;

    /*Ограничение по кол-ву строк*/
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;

    @media(max-width: 1330px) {
      margin-bottom: 25px;
    }
    @media(max-width: 1024px) {
      font-size: 16px;
    }
    @media(max-width: 767px) {
      font-size: 14px;
    }
  }


}

